import '../style.css';
import ReactDOM from 'react-dom';
import Start from './Start';
import firebase from 'firebase';
import { green } from '@material-ui/core/colors';

var EmployeesRegUser;
var EmployeesReg;
var kodas;
var password;
var Data;
export function RegCheck(key) {
    return(
      kodas = document.getElementById('code').value,
      password = document.getElementById('password').value,
      EmployeesRegUser = firebase.database().ref("User").orderByChild("userid").equalTo(kodas),
      EmployeesRegUser.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
            Data = childSnapshot.val();
        });
        if(Data === undefined){
          if((kodas.length >= 8)&&(kodas.length <= 20)){
            if((password.length >= 8)&&(password.length <= 20)){
              EmployeesReg = firebase.database().ref("User/" + key);
              EmployeesReg.update({
                userid: kodas,
                password: password
              });
              document.getElementById("reg-fin").style.color = green;
              document.getElementById("reg-fin").innerHTML = "Vartotojas sekmingai priregistruotas";
            }
            else document.getElementById("reg-fin").innerHTML = "Vartotojo slaptaodis turi buti nuo 8 iki 20 simboli";
          }
          else document.getElementById("reg-fin").innerHTML = "Vartotojo vardas turi buti nuo 8 iki 20 simboli";
        }
        else document.getElementById("reg-fin").innerHTML = "Toks vartotojo vardas jau yra";
      })  
    );  
};

function RegisterFinish(props) {
  return (
    <div className="container page-login">
      <div className="small-container">
        <div id="user" className="content">
          <h2>Registracija</h2>
          <div>
            <div className="name">Norimas prisijungimo vardas</div>
            <input id="code" placeholder="Prisijungimo vardas" className="input-box" type="text" maxLength="20" />
          </div>
          <div>
            <div className="password">Slaptaodis</div>
            <input id="password" placeholder="Slaptaodis" className="input-box pw" type="password" maxLength="20" />
          </div>
          <div id="reg-fin"></div>
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => RegCheck(props.id)}>Registruotis</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RegisterFinish;