import '../style.css';
import firebase from 'firebase';
import ReactDOM from 'react-dom';
import Start from './Start';
import RegisterFinish from './Register-finish';

var EmployeesRegCheck;
var kodas;
var idkodas;
var Data;
var key;
export function RegCheck() {
    return(
        kodas = document.getElementById('code').value,
        idkodas = document.getElementById('tempcode').value,
        EmployeesRegCheck = firebase.database().ref("User").orderByChild("code").equalTo(kodas),
        EmployeesRegCheck.once('value', (snapshot) => {
          snapshot.forEach((childSnapshot) => {
              Data = childSnapshot.val();
              key = childSnapshot.key;
          });
          if(kodas.length < 11){
            if((Data !== undefined)&&(idkodas === Data.userid)){
              if((kodas === Data.code)&&(idkodas === Data.userid)){
                ReactDOM.render(<RegisterFinish id={key}/>, document.getElementById('root'))
              }
            }
            else document.getElementById("reg-error").innerHTML = "Indentifikacinis kodas neteisingas";
          }
          else document.getElementById("reg-error").innerHTML = "Asmens kodas turi 11 skaitmen";
        })
    );  
};

function Register() {
  return (
    <div className="container page-login">
      <div className="small-container">
        <div id="user" className="content">
          <h2>Registracija</h2>
          <div>
            <div className="name">Asmens kodas</div>
            <input id="code" placeholder="Asmens kodas" className="input-box" type="text" minLength="11" maxLength="11"/>
          </div>
          <div>
            <div className="password">Indentifikacinis kodas</div>
            <input id="tempcode" placeholder="Indentifikacininis kodas" className="input-box pw" type="text" maxLength="6" />
          </div>
          <div id="reg-error"/>
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => RegCheck()}>Registruotis</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;