import * as React from 'react';
import firebase from 'firebase';
import ReactDOM from 'react-dom';
import '../ControlPanelStyle.css';
import MaterialTable from 'material-table';
import PDF from './PDF';
import { forwardRef } from 'react';
import maildata from '../maildata.json';
import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
};

var row;
var GetTableData;
var GetCompany;
var Data;
var Data1;
var array = [];
var rows = [];
var comp;
var role;
var actionsn;
var vardas;
export default function VacationTable(props) {
  const { useState } = React;
  const [selectedRow, setSelectedRow] = useState(null);
  return (
    array.length = 0,
    GetCompany = firebase.database().ref("Staff").orderByKey().equalTo(props.id),
    GetCompany.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data1 = childSnapshot.val();
          comp = Data1.company;
          role = Data1.role;
          vardas = Data1.name + " " + Data1.surname;
      });
      GetTableData = firebase.database().ref("Vacations");
      GetTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
          for(var i = 0; i <= comp.length; i = i + 11){
            if(comp.slice(i, i + 9) === Data.company){
              if((role === "vadovas")&&(Data.status === 1)){
                row ={
                  id: Data.id,
                  name: Data.name,
                  surname: Data.surname,
                  type: Data.type,
                  date: Data.date,
                  startdate: Data.startDate,
                  enddate: Data.endDate,
                  duration: Data.duration,
                  company: Data.company,
                  confirmedBy: Data.confirmedBy
                };
                array.push(row);
              }
              if((role === "buhalteris")&&(Data.status === 2)){
                row ={
                  id: Data.id,
                  name: Data.name,
                  surname: Data.surname,
                  type: Data.type,
                  date: Data.date,
                  startdate: Data.startDate,
                  enddate: Data.endDate,
                  duration: Data.duration,
                  company: Data.company,
                  confirmedBy: Data.confirmedBy
                };
                array.push(row);
              }
            }
          } 
        });
        rows = array.map((arr) => arr);
        if(role === "vadovas"){
          actionsn=[
            {
              icon: Check,
              tooltip: 'Patvirtinti',
              onClick: (event, rowData) => {
                console.log(vardas);
                Confirm(rowData.id, props.id, vardas);
                ReactDOM.render(<VacationTable id={props.id}/>, document.getElementById('panelroot'));
              }
            },
            {
              icon: Clear,
              tooltip: 'Atmesti',
              onClick: (event, rowData) => {
                Deny(rowData.id, props.id);
                ReactDOM.render(<VacationTable id={props.id}/>, document.getElementById('panelroot'));
              }
            }
          ]
          //ReactDOM.render(<VacationTable id={props.id}/>, document.getElementById('panelroot'));
        }
        if(role === "buhalteris"){
          actionsn=[
            {
              icon: SaveAlt,
              tooltip: 'Atsisisti',
              onClick: (event, rowData) => {
                ReactDOM.render(<PDF data={rowData}/>, document.getElementById('panelroot'));
              }
            },
            {
              icon: Check,
              tooltip: 'Patvirtinti',
              onClick: (event, rowData) => {
                Send(rowData.id, props.id);
                ReactDOM.render(<VacationTable id={props.id}/>, document.getElementById('panelroot'));
              }
            }
          ]
          //ReactDOM.render(<VacationTable id={props.id}/>, document.getElementById('panelroot'));
        }
      })
    }),
    <div>
      <div style={{ maxWidth: '100%' }}>
          <MaterialTable
            icons={tableIcons}
            localization={{
              pagination: {
                labelRowsSelect: 'eiluts',
                firstTooltip: 'Pirmas puslapis',
                nextTooltip: 'Sekantis puslapis',
                previousTooltip: 'Ankstesnis puslapis',
                lastTooltip: 'Paskutinis puslapis'
              },
              toolbar: {
                searchPlaceholder: 'Iekoti',
                searchTooltip: 'Iekoti'
              },
              header: {
                actions: 'VEIKSMAI'
              },
              body: {
                emptyDataSourceMessage: 'Nra kelt duomen',
                filterRow: {
                  filterTooltip: 'Filtruoti'
                },
                addTooltip: 'Pridti nauj',
                editRow: {
                  deleteText: 'Ar tikrai norite itrinti vartotoj?',
                  cancelTooltip: 'Ataukti',
                  saveTooltip: 'Patvirtinti'
                }
              }
          }}
            columns={[
              { title: 'ID', field: 'id', type: 'numeric', editable: 'never' },
              { title: 'VARDAS', field: 'name', editable: 'never' },
              { title: 'PAVARD', field: 'surname', editable: 'never' },
              { title: 'TIPAS', field: 'type', editable: 'never' },
              { title: 'PATEIKIMO DATA', field: 'date', editable: 'never' },
              { title: 'PRADIOS DATA', field: 'startdate', editable: 'never' },
              { title: 'PABAIGOS DATA', field: 'enddate', editable: 'never' },
              { title: 'TRUKM', field: 'duration', editable: 'never' },
              { title: 'MON', field: 'company', editable: 'never' },
              { title: 'PATVIRTINO', field: 'confirmedBy', editable: 'never' }
            ]}
            data={rows}
            title="Atostog sraas"
            onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
            options={{
              actionsColumnIndex: -1,
              pageSizeOptions: [5, 10],
              pageSize: 10,
              addRowPosition: 'first',
              filtering: true,
              headerStyle: {
                fontWeight: 'bold',
                fontSize: '16px'
              },
              titleStyle:{
                fontWeight: 'bold'
              },
              rowStyle: rowData => ({
                backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
              })
            }}
            
            actions={actionsn}

          />
        </div>
        <button className="page-buttons" onClick={() => ReactDOM.render(<VacationTable id={props.id}/>, document.getElementById('panelroot'))}>kelti / Atnaujinti duomenis</button>
      </div>
  );
}
var ConfirmVac;
var DenyVac;
var SendVac;
var ConfirmVacID;
var DenyVacID;
var SendVacID;
var CID;
var DID;
var SID;
var check;
var GetSecEmail;
var GetUserEmail;
var SecEmail;
var UserEmail;
function Confirm(ID, pID, vardas){
  return (
    check = 0,
    ConfirmVacID = firebase.database().ref("Vacations").orderByChild("id").equalTo(ID),
    ConfirmVacID.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        CID = childSnapshot.key;
        Data = childSnapshot.val();
      });
      ConfirmVac = firebase.database().ref("Vacations/" + CID)
      ConfirmVac.update({
        status: 2,
        confirmedBy: vardas
      })
      check = check + 1;
      if(check === 1){
        GetSecEmail = firebase.database().ref("Staff").orderByChild("role").equalTo("buhalteris");
        GetSecEmail.once('value', (snapshot) => {
          snapshot.forEach((childSnapshot) => {
            SecEmail = childSnapshot.val();
            for(var i = 0; i <= SecEmail.company.length; i = i + 11){
              if(SecEmail.company.slice(i, i + 9) === Data.company){
                sendEmailConfirm(pID, SecEmail.email, vardas)
              }
            }
          });
        
        })
      }
    })
  );
}

function Deny(ID, pID){
  return (
    check = 0,
    DenyVacID = firebase.database().ref("Vacations").orderByChild("id").equalTo(ID),
    DenyVacID.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        DID = childSnapshot.key;
        Data = childSnapshot.val();
      });
      check = check + 1;
      if(check === 1){
        GetUserEmail = firebase.database().ref("User").orderByKey().equalTo(Data.user);
        GetUserEmail.once('value', (snapshot) => {
          snapshot.forEach((childSnapshot) => {
            UserEmail = childSnapshot.val();
          });
          sendEmailDenied(pID, UserEmail.email, DID);
        })
      }
    })
  );
}
var UserD;
var UserData;
var DeductDays;
var UserDays;
var newdays;
var count;
function Send(ID, pID){
  return (
    check = 0,
    count = 0,
    SendVacID = firebase.database().ref("Vacations").orderByChild("id").equalTo(ID),
    SendVacID.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        SID = childSnapshot.key;
        UserD = childSnapshot.val();
      });
      UserDays = firebase.database().ref("User").orderByKey().equalTo(UserD.user)
      UserDays.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          UserData = childSnapshot.val();
        });
          SendVac = firebase.database().ref("Vacations/" + SID);
          SendVac.update({
            status: 3
          })
          if(count === 0){
            newdays = UserData.days - UserD.workDays;
            count = count + 1;
          }
          DeductDays = firebase.database().ref("User/" + UserD.user);
          DeductDays.update({
            days: newdays
          })
          check = check + 1;
          if(check === 1){
            GetUserEmail = firebase.database().ref("User").orderByKey().equalTo(Data.user);
            GetUserEmail.once('value', (snapshot) => {
              snapshot.forEach((childSnapshot) => {
                UserEmail = childSnapshot.val();
              });
              sendEmailDone(pID, UserEmail.email);
            })
          }
      });
    })
  );
}
function sendEmailConfirm(pID, SecEmail, vardas) {
  window.Email.send({
    Host: "smtp.gmail.com",
    Username: maildata.emailname,
    Password: maildata.emailpassword,
    To: SecEmail,
    From: maildata.emailname,
    FromName: "Transkelta",
    Subject: "Atostog praymas",
    Body: "Darbuotojo praymas ieiti atostog buvo patvirtintas. Praome patikrinti programl"
  })
    .then(function (message) {
      ReactDOM.render(<VacationTable id={pID}/>, document.getElementById('panelroot'));
      alert("Praymas patvirtintas")
  });
}

function sendEmailDone(pID, UserEmail) {
  window.Email.send({
    Host: "smtp.gmail.com",
    Username: maildata.emailname,
    Password: maildata.emailpassword,
    To: UserEmail,
    From: maildata.emailname,
    FromName: "Transkelta",
    Subject: "Atostog praymas",
    Body: "Js praymas buvo patvirtintas"
  })
    .then(function (message) {
      ReactDOM.render(<VacationTable id={pID}/>, document.getElementById('panelroot'));
      alert("Praymas patvirtintas")
  });
}

function sendEmailDenied(pID, UserEmail, DID) {
  DenyVac = firebase.database().ref("Vacations/" + DID)
  DenyVac.remove()
  window.Email.send({
    Host: "smtp.gmail.com",
    Username: maildata.emailname,
    Password: maildata.emailpassword,
    To: UserEmail,
    From: maildata.emailname,
    FromName: "Transkelta",
    Subject: "Atostog praymas",
    Body: "Js praymas buvo atmestas"
  })
    .then(function (message) {
      ReactDOM.render(<VacationTable id={pID}/>, document.getElementById('panelroot'));
      alert("Praymas atmestas")
  });
}